#sideNull{
    display: none;
}

#sideCon{ 
    width: 100vw;
    height: 100vh;
    position: absolute;
    height: -webkit-fit-available;
    display: flex;
}

    #sideCon0{
        flex: 5;
        max-width: 250px;
        display: flex;
        flex-direction: column;
        background-color: darkorange;
    }

        #sideCon0Child0{
            width: 100%;
            height: 50px;
            background-color: red;
        }

        #sideCon0Child1{
            flex: 15;
            display: flex;
            flex-wrap: wrap;
            overflow: scroll;
        }

            #sideCon0Child1Child0{
                width: 100%;
                height: 100px;
                position: relative;
            }

                #sideCon0Child1Child0Child0{
                    width: 100px;
                    height: 100px;
                    object-fit: cover;
                    border-radius: 200px;
                }

                #sideCon0Child1Child0Child1{
                    width: 100%;
                    height: 100%;
                    position: absolute;
                }

            #sideCon0Child1Child1{
                width: 100%;
                height: 200px;
                position: relative;
            }

                #sideCon0Child1Child1Child0{

                }

                #sideCon0Child1Child1Child1{
                    width: 100%;
                    height: 100%;
                    position: absolute;
                }

            #sideCon0Child1Child2{
                width: 100%;
                height: 200px;
            }

        #sideCon0Child2{
            width: 100%;
            height: 50px;
            background-color: red;
        }

    #sideCon1{
        flex: 1;
        position: relative;
        background-color: black;
        opacity: 50%;
    }

        .sideCon1{
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0%;
        }