#userCon{
    flex: 1;
    display: flex;
    flex-direction: column;
}

    #userCon0{
        height: 50px;
        display: flex;
        background-color: darkgoldenrod;
    }

        #userCon0Child0{
            flex: 1;
        }

        #userCon0Child1{
            flex: 5;
        }

        #userCon0Child2{
            flex: 1;
        }

    #userCon1{
        flex: 10;
        display: flex;
        flex-wrap: wrap;
        overflow: scroll;
        background-color: darkorange;
    }

        #userCon1Child0{
            width: 100%;
            height: 200px;
            display: flex;
        }

            .userCon1Child0{
                flex: 1;
                position: relative;
            }

                .userCon1Child0Child0{
                    width: 150px;
                    height: 150px;
                    object-fit: cover;
                    border-radius: 200px;
                }

        #userCon1Child1{
            width: 100%;
            height: 200px;
        }

    #userCon2{
        height: 50px;
        display: flex;
        background-color: darkgoldenrod;
    }

    #userCon2Child0{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        background-color: darkgoldenrod;
    }

        .userCon2Child0{
            width: 55%;
            height: 55%;
            object-fit: contain;
        }

        #userCon2Child1{
            flex: 1;
        }

        #userCon2Child2{
            flex: 1;
        }

        #userCon2Child3{
            flex: 1;
        }